import React, { useState } from "react";
import { Plus, Trash2 } from "react-feather";

function DynamicImages() {
    const [inputList, setInputList] = useState([{ image: "" }]);
    const [file, setFile] = useState({ image: "" });


    // handle input change
    const handleInputChange = (e, index) => {
        const { name, value } = e.target;
        console.log(name,value);
        const list = [...inputList];
        console.log(list);
        // list[index][name] = value;
        // setInputList(list);
    };

    // handle click event of the Remove button
    const handleRemoveClick = index => {
        const list = [...inputList];
        list.splice(index, 1);
        setInputList(list);
    };

    // handle click event of the Add button
    const handleAddClick = () => {
        setInputList([...inputList, { image: "" }]);
    };

    return (
        <>
            {inputList.map((x, i) => {
                return (
                    <>
                        <div className="row row-sm mg-t-10 mx-1" key={Math.random()*100}>
                            <div className="col-sm-9">
                                <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                                    Image
                                </label>
                                {/* <div className="px-2"> */}
                                    <input
                                        type="file"
                                        // className="custom-file-input"
                                        // id="customFile"
                                        // name="image"
                                        // value={inputList.image}
                                        // onChange={handleInputChange}
                                        

                                    />
                                    {/* <label className="custom-file-label" for="customFile2">
                                        Choose file
                                    </label> */}
                                {/* </div> */}
                            </div>

                            <div className="col-sm-3" style={{ flexDirection: 'row' }}>
                                {inputList.length !== 1 && <button
                                    className="btn btn-sm btn-white mg-t-25"
                                    onClick={() => handleRemoveClick(i)}>
                                    <Trash2/>
                                    </button>}
                                {inputList.length - 1 === i && <button
                                    className="btn btn-sm btn-white mg-t-25"
                                    onClick={handleAddClick}>
                                    <Plus/>
                                    </button>}
                            </div>
                        </div>
                    </>
                );
            })}

        </>
    );
}

export default DynamicImages;