import { Modal } from "react-bootstrap";
import React, { Component } from "react";
import axios from "axios";

export default class ProfitMargin extends Component {
    state = {
        isOpenChange: false,
        twoPlan: "",
        fifteenPlan: "",
        thirtyPlan: "",
        unit: "%"
    };
    hideModal = () => {
        this.setState({ isOpenChange: false });
    };
    showModal = () => {
        this.setState({ isOpenChange: true });
    };

    onChangeHandler = (e) => {
        const value = e.target.value;
        this.setState({
            [e.target.name]: value,
        });
    }
    onChangeSelector = (e) => {
        const value = e.target.value;
        this.setState({
            [e.target.name]: value,
        });
    }
    onSubmit = (e) => {
        e.preventDefault();
        const data = this.state;
        const newPlan = {
            twoPlan: data.twoPlan + data.unit0,
            fifteenPlan: data.fifteenPlan + data.unit1,
            thirtyPlan: data.thirtyPlan + data.unit2,
        };
        axios.post('/api/plans/6066360c920a2e311c95ee92', newPlan).then((res) => {
            console.log('Data Added Successfully')
        }).catch((err) => {
            console.error(err)
        })
    }

    render() {
        return (
            <div className="contact-content p-3 ml-n5">
                <div
                    data-label="Profit Margin "
                    className="df-example demo-forms mb-4"
                >
                    <form onSubmit={this.onSubmit}>
                        <div className="d-flex justify-content-end ">
                            <div className="px-2">
                                <button
                                    className="btn btn-sm  btn-icon btn-white px-4 mx-1"
                                >Cancel</button>
                                <button
                                    className="btn btn-sm  btn-icon btn-primary px-4 mx-1"
                                    onPress={() => this.onSubmit}
                                >Save</button>
                            </div>
                        </div>
                        <div className="p-3">
                            <div className="row mt-2">
                                <label className="labels">2 Days Plan</label>
                                <div className="input-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Profit Margin"
                                        name="twoPlan"
                                        onChange={this.onChangeHandler}
                                    />
                                    <div className="input-group-append">
                                        <select className="input-group-text" name="unit0" onChange={this.onChangeSelector} defaultValue="%" >
                                            <option name="unit0" value="%" >%</option>
                                            <option name="unit0" value="$" >$</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="row mt-2">
                                <label className="labels">15 Days Plan</label>
                                <div className="input-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Profit Margin"
                                        name="fifteenPlan"
                                        onChange={this.onChangeHandler}
                                    />
                                    <div className="input-group-append">
                                        <select className="input-group-text" name="unit1" onChange={this.onChangeSelector} defaultValue="%" >
                                            <option name="unit1" value="%">%</option>
                                            <option name="unit1" value="$">$</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="row mt-2">
                                <label className="labels">30 Days Plan</label>
                                <div className="input-group">
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder="Profit Margin"
                                        name="thirtyPlan"
                                        onChange={this.onChangeHandler}
                                    />
                                    <div className="input-group-append">
                                        <select className="input-group-text" name="unit2" onChange={this.onChangeSelector} defaultValue="%" >
                                            <option name="unit2" value="%">%</option>
                                            <option name="unit2" value="$">$</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
                <Modal
                    show={this.state.isOpenChange}
                    onHide={this.hideModal}
                    className="modal fade"
                >
                    <Modal.Header className="modal-header">
                        <h4 className="modal-title">Change Password</h4>
                        <button
                            type="button"
                            onClick={this.hideModal}
                            className="close"
                            data-dismiss="modal"
                            aria-hidden="true"
                        >
                            
            </button>
                    </Modal.Header>
                    <Modal.Body className="modal-body">
                        <div className="row">
                            <div className="col">
                                <div className="form-group">
                                    <label>Current Password</label>
                                    <input
                                        className="form-control"
                                        type="password"
                                        placeholder=""
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <div className="form-group">
                                    <label>New Password</label>
                                    <input
                                        className="form-control"
                                        type="password"
                                        placeholder=""
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <div className="form-group">
                                    <label>
                                        Confirm <span className="d-none d-xl-inline">Password</span>
                                    </label>
                                    <input
                                        className="form-control"
                                        type="password"
                                        placeholder=""
                                    />
                                </div>
                            </div>
                        </div>
                    </Modal.Body>
                    <Modal.Footer className="modal-footer">
                        <div className="row">
                            <div className="col-md-6">
                                <input
                                    type="button"
                                    onClick={this.hideModal}
                                    className="btn btn-white"
                                    data-dismiss="modal"
                                    defaultValue="Cancel"
                                />
                            </div>
                            <div className="col-md-6">
                                <input
                                    type="submit"
                                    value="Delete"
                                    className="btn btn-danger"
                                    defaultValue="Delete"
                                    onClick={this.deletestaff}
                                />
                            </div>
                        </div>
                    </Modal.Footer>
                </Modal>
            </div>
        );
    }
}
